import React, { useContext } from 'react';
import styled from 'styled-components';
import { Container, Row, Col } from 'react-grid-system';
import OfficeContext from '../../_context/office-context';

const HeroCont = styled.div`
  position: relative;
  height: calc(100vh - 89px);
  overflow: hidden;
  color: ${props => props.theme.secondaryColor};
`
const HeroImg = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(${props => props.theme.heroUrl});
  width: 100%;
  height: 100%;
  &::after{
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, .5),rgba(0, 0, 0, .5)), linear-gradient(rgba(0, 0, 0, .5),rgba(0, 0, 0, .5));
  }
  z-index: -1;
`
const HeroContent = styled.div`
  height: calc(100vh - 89px);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`

const HeroTitle = styled.h1`
  font-weight: lighter;
`

export default ()=> {
  const hero = useContext(OfficeContext).webOffice.home.hero;
  return(
    <HeroCont>
      <HeroImg />
      <Container>
        <HeroContent>
          <HeroTitle>
            {hero.mainText}
          </HeroTitle>
        </HeroContent>
      </Container>
    </HeroCont>
  )
}